import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {HttpModule} from '@angular/http';
import {FormsModule} from '@angular/forms';
import {RouterModule, Routes} from '@angular/router';

import { AppComponent } from './app.component';
import { HomeComponent } from './component/home/home.component';
import { InfoComponent } from './component/info/info.component';
import { CourseComponent } from './component/course/course.component';
import { NavbarComponent } from './component/navbar/navbar.component';
import { VisualizeComponent } from './component/visualize/visualize.component';

// services
import { CourseService } from './service/course.service';
import { VisualizeService } from './service/visualize.service';
import { ChartsService } from './service/charts.service';

import { ReviewcoursesComponent } from './component/reviewcourses/reviewcourses.component';
import { ReviewcoursesService } from './service/reviewcourses.service';


const appRoutes: Routes = [ 
  {path: '', component: HomeComponent},
  {path: 'info', component:InfoComponent},
  {path: 'courses',component:CourseComponent},
  {path: 'reviewcourses',component:ReviewcoursesComponent},
  {path: 'visualize',component:VisualizeComponent}

]

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    InfoComponent,
    CourseComponent,
    NavbarComponent,
    ReviewcoursesComponent,
    VisualizeComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    RouterModule,
    FormsModule,
    RouterModule.forRoot(appRoutes)

  ],
  providers: [CourseService,ReviewcoursesService,ChartsService,VisualizeService],
  bootstrap: [AppComponent]
})
export class AppModule { }
