import { Component, OnInit } from '@angular/core';
import { VisualizeService } from '../../service/visualize.service' 
//import{FlashMessage} from 'angular-flash-message';
import {Router} from '@angular/router';
import { VisualizeDifficulty } from '../../visualize';
import { ChartsService } from '../../service/charts.service';
import { Chart } from 'chart.js';

var  visualizeDifficulty =[];
 var  visualizeDifficultyLabels = [];
  var  maxDifficulties = [];
  var  minDifficulties = [];

  var  maxCourseMaterial = [];

  var  maxProfFriendliness = [];
  var  maxCourseContent = [];
  var  uniqueVisualizeDifficultyLabels = [];
  var  colors = [];
@Component({
  selector: 'app-visualize',
  templateUrl: './visualize.component.html',
  //styleUrls: ['./visualize.component.css'],
  providers: [VisualizeService]
})

export class VisualizeComponent implements OnInit {
 
	piChart = [];// This will hold our chart info
	piChartCourseMaterial = [];// This will hold our chart info
	piChartProfFriendliness = [];// This will hold our chart info
	piChartCourseContent = [];// This will hold our chart info

 constructor(private visualizeService: VisualizeService,private _1st_chart: ChartsService) 
	{ 
	console.log('start visualization');
 }


  ngOnInit() {
   	this._1st_chart.getData() 
          .subscribe(res => {
        
        	setDataForVisualization(res);
        	createGraphs('canvas','bar',this.piChart,maxDifficulties,minDifficulties);
        	createGraphs('canvas1','bar',this.piChartCourseMaterial,maxCourseMaterial,minDifficulties);
        	createGraphs('canvas2','bar',this.piChartProfFriendliness,maxProfFriendliness,minDifficulties);
              createGraphs('canvas3','polarArea',this.piChartCourseContent,maxCourseContent,minDifficulties);
        
      })
  }
    
  }
//Function to create graphs
function createGraphs(canvas,type,chart,max,min)
{
chart = new Chart(canvas, {
          type: type,
          data: {
            labels: uniqueVisualizeDifficultyLabels,
            datasets: [
              { 
                data: max,
                fill: true,
                
                //borderColor: "#3cba9f",
                backgroundColor: colors
              },
              { 
                data: min,
               // borderColor: "#ffcc00",
                fill: true
              },
            ]
          },
          options: {
            legend: {
              display: true
            },
            scales: {
              xAxes: [{
                //display: false

                  display: true
              }],
              yAxes: [{
                display:true
              }],
            }
          }
        
 
      });    
        
}
function getRandomColor() {
  var letters = '0123456789ABCDEF';
  var color = '#';
  for (var i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
}

// Function to set dataset
  function setDataForVisualization(res){

 
  for(let i=0; i<res.length; i++)
  {
  	visualizeDifficultyLabels.push(res[i].course_name);
  	colors.push(getRandomColor());
  }
  console.log(colors);
  uniqueVisualizeDifficultyLabels = visualizeDifficultyLabels.filter(onlyUnique);
  for(let i=0; i<uniqueVisualizeDifficultyLabels.length; i++)
  {
  let diff = 0;
  let diffCourseMaterial = 0;
  let diffProfFriendliness = 0;
  let diffCourseContent = 0;
  let count =0;
 
  	for(let j=0; j<res.length; j++)
 	 {
 	 
 	 if(uniqueVisualizeDifficultyLabels[i] === res[j].course_name)
 	 	{
 	 		 	
 	 		diff = diff + parseInt(res[j].diff_rate,10);
 	 		diffCourseMaterial = diff + parseInt(res[j].course_material,10);
 	 		diffProfFriendliness = diff + parseInt(res[j].prof_friendli,10);
 	 		diffCourseContent = diff + parseInt(res[j].course_content,10);
 	 		count = count +1;
 	 		
 	 	}
 	 }
 	 console.log(diff);
  	maxDifficulties.push(diff/count);
  	maxCourseMaterial.push(diffCourseMaterial/count);
        maxProfFriendliness.push(diffProfFriendliness/count);
  	maxCourseContent.push(diffCourseContent/count);
  	  	  	  	
  	minDifficulties.push(0);
  	
  }

  }

function onlyUnique(value, index, self)
{

    return self.indexOf(value) === index;
}
