import { Injectable } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
//import { ENETUNREACH } from 'constants';


@Injectable({
  providedIn: 'root'
})

export class VisualizeService {

  constructor(private http:Http,) { }

  visualizeOnDifficulty()
  {
    return this.http.get('http://localhost:3000/api/courses').map(res => res.json());
  }

  visualizeOnCourseMaterial()
  {
  let headers = new Headers();
  headers.append('Content-type','application/json');
  return this.http.post('http://localhost:3000/api/course', {headers:headers})
  //return this.http.post('http://localhost:3000/api/course', Course, {headers:headers})
  .map(res  => res.json());
   }
   
   visualizeOnProfFriendliness()
  {
    return this.http.get('http://localhost:3000/api/courses')
    .map(res => res.json());
  }
  
  visualizeOnCourseContent()
  {
    return this.http.get('http://localhost:3000/api/courses')
    .map(res => res.json());
  }

}
